import validator from "validator";

export const checkEmailValid = (email) => {
  return validator.isEmail(email);
};

export const checkResetTokenValid = (token) => {
  return validator.matches(
    token,
    /^(\w{8})-(\w{4})-(\w{4})-(\w{4})-(\w{12})$/i
  );
};

export const checkPasswordValid = (password) => {
  return password.length > 5;
};

export const checkNameValid = (name) => {
  return name.length > 1;
};

export const getErrMsgForUser = (err) => {
  switch (err) {
    case "Email no valid": {
      return " e-mail";
    }
    case "User already exists": {
      return " e-mail ";
    }
    case "User with such email already exists": {
      return "   e-mail  ";
    }
    case "Name is empty": {
      return "  ( 2 )";
    }
    case "Pass no valid": {
      return "  ( 5 )";
    }
    case "Token no valid": {
      return "    ########-####-####-####-############";
    }
    case "Incorrect reset token": {
      return "       ";
    }
    case "email or password are incorrect": {
      return "    ( e-mail  )";
    }
    default: {
      return;
    }
  }
};
